@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,480;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,480;1,600;1,700;1,800;1,900&display=swap');


.learn{
    position: relative;
    height: 640px;
    font-weight: 480;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}
.learn::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./../images/manHoldingDrink.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: brightness(50%);
    z-index: -1;
}
.learn__container_overall{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    width: 100%;
    height: 100%;
}
.learn__title_text{
    font-size: 32px;
    font-weight: 600;
    line-height: 30px;
    color: #fff;
    text-align: center;
    margin-top: 85px;
}
.learn__container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;

}
.learn__content{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.learn-content-text{
    display: flex;
    align-items: center;
    gap: 10px;
}
.learn-content-text p{
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    font-family: "Montserrat Alternates", sans-serif;
}
.learn_content_button{
    width: 398px;
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    margin-top: 80px;
    padding: 20px;
    font-size: 16px;
    font-weight: 600;
    line-height: 19.5px;
    color: #fff;
    font-family: "Montserrat Alternates", sans-serif;
}